import { IProduct } from '../../productList';
import { IFilterList } from '../../productList';
import { catalog } from '../../productList';
import Products from '../items';

const products = new Products();
const SORTING_TYPES = {
  TEXT_UP: 'textUp',
  TEXT_DOWN: 'textDown',
  YEAR_UP: 'yearUp',
  YEAR_DOWN: 'yearDown',
}

const productList = <HTMLDivElement>document.querySelector('.product-list');

export const filterCatalog = function(filtersList: IFilterList): void {
  let newProducts = catalog.filter((el) => {
    const filterMarkLength = (filtersList.mark as string[]).length;
    const filterColorLength = (filtersList.color as string[]).length;
    const filterCameraLength = (filtersList.amountOfCamera as string[]).length;
    const filterNameLength = (filtersList.name as string).length;
    const filterName = (str: string) => {
      if((`${str}`).toLowerCase().includes((filtersList.name as string).toLowerCase())) {
        return true;
      }
      return false;
    }

    let isMarkFilter, isColorFilter, isCameraFilter, isPopularFilter, isAmountFilter, isYearFilter, isInputTextFilter;
    
    if((filterMarkLength > 0 && (filtersList.mark as string[]).includes(`${el.mark}`)) 
      || filterMarkLength === 0) {
        isMarkFilter = true;
    } else {
      isMarkFilter = false;
    }

    if((filterColorLength > 0 && (filtersList.color as string[]).includes(`${el.color}`))
      || filterColorLength === 0) {
        isColorFilter = true;
    } else {
      isColorFilter = false;
    }

    if((filterCameraLength > 0 && (filtersList.amountOfCamera as string[]).includes(`${el.amountOfCamera}`))
      || filterCameraLength === 0) {
        isCameraFilter = true;
    } else {
      isCameraFilter = false;
    }

    if((filtersList.isPopular === true && `${el.isPopular}` === 'true')
      || filtersList.isPopular === false) {
        isPopularFilter = true;
    } else {
      isPopularFilter = false;
    }

    if((+`${el.amount}`) >= +(filtersList.amount as string[])[0] 
      && (+`${el.amount}`) <= +(filtersList.amount as string[])[1]) {
        isAmountFilter = true;
    } else {
      isAmountFilter = false;
    }

    if((+`${el.year}`) >= +(filtersList.year as string[])[0]
      && (+`${el.year}`) <= +(filtersList.year as string[])[1]) {
        isYearFilter = true;
    } else {
      isYearFilter = false;
    }

    if((filterNameLength > 0 && ( filterName(el.name) || filterName(el.mark) ))
      || filterNameLength === 0) {
        isInputTextFilter = true;
    } else {
      isInputTextFilter = false;
    }

    return isMarkFilter && isColorFilter && isCameraFilter && isPopularFilter && isAmountFilter && isYearFilter && isInputTextFilter;
  });

  productList.innerHTML = '';

  if (newProducts.length === 0) {
    productList.innerHTML = `<h2>Извините совпадений не найдено</h2>`;
  } else {
    const newProductsSort = JSON.parse(JSON.stringify(newProducts));

    switch(filtersList.sorting) {
      case SORTING_TYPES.TEXT_UP:
        newProductsSort.sort((a: IProduct, b: IProduct) => a.name > b.name ? 1 : -1);
        break;
      case SORTING_TYPES.TEXT_DOWN:
        newProductsSort.sort((a: IProduct, b: IProduct) => b.name > a.name ? 1 : -1);
        break;
      case SORTING_TYPES.YEAR_UP:
        newProductsSort.sort((a: IProduct, b: IProduct) => +a.year - +b.year);
        break;
      case SORTING_TYPES.YEAR_DOWN:
        newProductsSort.sort((a: IProduct, b: IProduct) => +b.year - +a.year);
        break;
      default:
    }
    products.draw(newProductsSort);
  }
}
