import { IFilterList } from '../../productList';
import { dafaultFilterList } from '../../productList';
import { renderFilters }  from './renderFilters';
import { filterCatalog } from './filterCatalog';
import './filter.css';

const filtersBlock = <HTMLDivElement>document.querySelector('.filters');
const filterInput = <HTMLInputElement>document.querySelector('#input');
const filterSelect = <HTMLSelectElement>document.querySelector('.filter-select');
const filtersReset = <HTMLButtonElement>document.querySelector('.reset-filters');
const settingsReset = <HTMLButtonElement>document.querySelector('.reset-settings');
let filters: IFilterList = {};

if (localStorage.getItem('filters') !== null) {
  filters = JSON.parse(localStorage.getItem('filters') as string);
  renderFilters(filters);
  filterCatalog(filters);
} else {
  filters = JSON.parse(JSON.stringify(dafaultFilterList));
}

filtersBlock.addEventListener('click', (e: Event) => {
  if ((e.target as HTMLObjectElement).hasAttribute('data-filter')) {
    const filter = (e.target as HTMLElement).dataset.filter;
    if ((e.target as Element).classList.contains('filter-btn')) {
      (e.target as Element).classList.toggle('filter-active');
      const index = (filters[`${filter}`] as Array<string>).indexOf(`${(e.target as HTMLElement).innerText}`);
      if (index === -1) {
        (filters[`${filter}`] as Array<string>).push((e.target as HTMLElement).innerText);
      } else {
        (filters[`${filter}`] as Array<string>).splice(+`${index}`, 1)
      }
    }
    if ((e.target as Element).classList.contains('filter-check')) {
      (filters[`${filter}`] as boolean) = (e.target as HTMLInputElement).checked;
    }
    if (filter === 'clear') {
      filterInput.value = '';
      filterInput.focus();
      filters.name = '';
    }

    localStorage.setItem('filters', JSON.stringify(filters));
    filterCatalog(filters);
  }
  
});

filterInput.addEventListener('input', (ev: Event) => {
  filters.name = (ev.target as HTMLInputElement).value;
  localStorage.setItem('filters', JSON.stringify(filters));
  filterCatalog(filters);
});

filterSelect.addEventListener('change', (ev: Event) => {
  filters.sorting = (ev.target as HTMLSelectElement).value;
  localStorage.setItem('filters', JSON.stringify(filters));
  filterCatalog(filters);
});

filtersReset.addEventListener('click', () => {
  let actualSorting = filters.sorting;
  filters = JSON.parse(JSON.stringify(dafaultFilterList));
  filters.sorting = actualSorting;
  localStorage.setItem('filters', JSON.stringify(dafaultFilterList));
  renderFilters(filters);
  filterInput.focus();
  filterCatalog(filters);
});

settingsReset.addEventListener('click', () => {
  localStorage.clear();
  location.reload();
});

function rangeInputChangeEventHandler(e:Event):void {
  const target = e.target as HTMLInputElement;
  const origin = target.classList;
  const rangeName = target.getAttribute('name');
  const RANGE_TYPES = {
    RANGE_ONE: 'range_1',
    RANGE_TWO: 'range_2',
    RANGE_THREE: 'range_3',
    RANGE_FOUR: 'range_4',
  };
    
  const parent = target.parentElement;
  const minBtn = parent?.children[0] as HTMLInputElement;
  const maxBtn = parent?.children[1] as HTMLInputElement;
  const rangeMin = parent?.children[2] as HTMLSpanElement;
  const rangeMax = parent?.children[3] as HTMLSpanElement;

  let minValue = +minBtn.value;
  let maxValue = +maxBtn.value;

  const inputLength = +target.max - +target.min;
  let inputLeftVal = (minValue - +target.min) / inputLength * 100;
  let inputRightVal = (maxValue - +target.min) / inputLength * 100;

  if(origin.contains('min') && minValue > maxValue - 1){      
      minBtn.value = `${maxValue - 1}`;
  }
  minValue = +minBtn.value;
  
  if(origin.contains('max') && maxValue - 1 < minValue){
      maxBtn.value = `${minValue + 1}`;
  }
  maxValue = +maxBtn.value;

  switch(rangeName) {
    case RANGE_TYPES.RANGE_ONE:
      rangeMin.innerText = `${minValue}`;
      (filters.amount as string[])[0] = `${minValue}`;
      break;
    case RANGE_TYPES.RANGE_TWO:
      rangeMax.innerText = `${maxValue}`;
      (filters.amount as string[])[1] = `${maxValue}`;
      break;
    case RANGE_TYPES.RANGE_THREE:
      rangeMin.innerText = `${minValue}`;
      (filters.year as string[])[0] = `${minValue}`;
      break;
    case RANGE_TYPES.RANGE_FOUR:
      rangeMax.innerText = `${maxValue}`;
      (filters.year as string[])[1] = `${maxValue}`;
      break;
    default:
  }
  
  Array.from((target.parentElement as HTMLElement).children as HTMLCollection).forEach((el) => {
    if (el.classList.contains('input-range')) {
      (el as HTMLInputElement).style.background = `linear-gradient(to right, white 0%, white ${inputLeftVal}%, #003d7c ${inputLeftVal}%, #003d7c ${inputRightVal}%, white ${inputRightVal}%, white 100%)`;
    }
  });

  localStorage.setItem('filters', JSON.stringify(filters));
  filterCatalog(filters);
}

document.querySelectorAll('input[type="range"]')[0].addEventListener('input', rangeInputChangeEventHandler);
document.querySelectorAll('input[type="range"]')[1].addEventListener('input', rangeInputChangeEventHandler);
document.querySelectorAll('input[type="range"]')[2].addEventListener('input', rangeInputChangeEventHandler);
document.querySelectorAll('input[type="range"]')[3].addEventListener('input', rangeInputChangeEventHandler);

