const fullCart = 'Извините, все слоты заполнены :(';
const maxProductsCart = 20;

class LocalStorageUtil {
  private keyName: string;

  constructor() {
    this.keyName = 'products';
  }

  getProducts() {
    const products = localStorage.getItem(this.keyName);
    if (products !== null) {
      return JSON.parse(products);
    }
    return [];
  }

  putProducts(id: string | undefined) {
    const products = this.getProducts();
    const index = products.indexOf(id);
    const productsLength = products.length;
    let isPushProduct = false;

    if (index === -1) {
      if (productsLength < maxProductsCart) {
        products.push(id);
        isPushProduct = true;
      } else {
        isPushProduct = false;
        alert(fullCart);
      }
    } else {
      products.splice(index, 1);
      isPushProduct = false;
    }

    localStorage.setItem(this.keyName, JSON.stringify(products));

    return { isPushProduct, products, };
  }
}

export default LocalStorageUtil;
