import { IProduct } from '../productList';
import LocalStorageUtil from './localStorageUtil/localStorageUtil';
import Cart from './cart/cart';
import './items.css';

class Products {
  private localStorageUtil: LocalStorageUtil;
  private cart: Cart;
  private itemIsActive: string;
  private btnIsActive: string;
  private addLabel: string;
  private deletionLabel: string;

  constructor() {
    this.localStorageUtil = new LocalStorageUtil();
    this.cart = new Cart();
    this.itemIsActive = 'item-active';
    this.btnIsActive = 'btn-active';
    this.addLabel = 'Добавить в корзину';
    this.deletionLabel = 'Удалить из корзины';
  }

  draw(data: IProduct[]) {
    const productList = <HTMLDivElement>document.querySelector('.product-list');
    const productStore = this.localStorageUtil.getProducts() as string[];

    this.cart.view(`${productStore.length}`);

    data.forEach((item: IProduct) => {
      let itemClass = '';
      let btnClass = '';
      let btnText = '';

      if (productStore.indexOf(item.id) === -1) {
        btnText = this.addLabel;
      } else {
        btnText = this.deletionLabel;
        itemClass = this.itemIsActive;
        btnClass = ' ' + this.btnIsActive;
      }
      const productItem = <HTMLDivElement>document.createElement("div");
      if (itemClass !== '') {
        productItem.classList.add('product-item', `${itemClass}`);
      } else {
        productItem.classList.add('product-item');
      }
      productItem.innerHTML = 
      `<div class="front">
        <img src="${item.src}" alt="${item.alt}">
        <h3>${item.name}</h3>
      </div>
      <div class="back">
        <h2>${item.name}</h2>
        <ul class="list list-reset">
            <li>Количество: ${item.amount}</li>
            <li>Год выхода: ${item.year}</li>
            <li>Производитель: ${item.mark}</li>
            <li>Цвет: ${item.color}</li>
            <li>Количество камер: ${item.amountOfCamera}</li>
        </ul>
        <button class="product-btn btn-reset${btnClass}" data-id='${item.id}'>${btnText}</button>
      </div>`
      productList.append(productItem);
    });
    
    this.cart.showModal(productStore);
  }

  setProductStorage() {
    const productList = <HTMLDivElement>document.querySelector('.product-list');
    productList.addEventListener('click', (e: Event) => {
      if((e.target as HTMLButtonElement).classList.contains('product-btn')) {
        const { isPushProduct, products} = this.localStorageUtil.putProducts(`${(e.target as HTMLButtonElement).dataset.id}`);
        if(isPushProduct) {
          (e.target as HTMLButtonElement).innerText = this.deletionLabel;
          (e.target as HTMLButtonElement).classList.add('btn-active');
          ((e.target as HTMLButtonElement).closest('.product-item') as HTMLDivElement).classList.add('item-active');
        } else {
          (e.target as HTMLButtonElement).innerText = this.addLabel;
          (e.target as HTMLButtonElement).classList.remove('btn-active');
          ((e.target as HTMLButtonElement).closest('.product-item') as HTMLDivElement).classList.remove('item-active');
        }
        this.cart.view(`${products.length}`);
        this.cart.showModal(products);
      }
    });
  }
}

export default Products;
